<template lang="pug">
  section
    ul
      li(id="article-list" v-for="article in articleList")
        a(:href="article.uri").clear-color
          .card.grey.lighten-4
            .card-content
              span.card-title.article-title {{ article.title }}
              div.chip.auther
                img(:src="\"/static/images/\"+article.auther+\".jpeg\"" alt="Auther ")
                | auther: {{ article.auther }}
              div(v-for="tag in article.tags").chip 
                a(:href="\"/#/tags/\"+tag").tag \#{{ tag }}
              p.description {{ article.description }}
</template>

<script>
import articleInfo from '../../static/articles.json'

export default {
  props: ['count', 'tagFilter', 'tag'],
  name: 'PostList',
  data () {
    return {
    }
  },
  computed: {
    articleList () {
      var vm = this
      return articleInfo
        .slice(0, Math.min(vm.count, articleInfo.length))
        .filter((element, index, array) => { return !(vm.tagFilter) || element.tags.indexOf(vm.tag) >= 0 })
    }
  }
}
</script>

<style lang="stylus">
p
  margin 1rem 0rem
  font-size 1.1rem
.clear-color
  color #222

.description
  margin 0.6rem 0.2rem !important
</style>




